
export default {
    state: {
        workTime: 1500,
        restTime: 300,    
        currentTime: 1500,
        running: false,
        statusSwitch: false,
        tomaEvent: false, // 控制 continue、 complete
        todoRef: firebase.database()
    },
    actions: {
        countDown({ state, commit, dispatch}) {
            state.running = setInterval(count, 1000)
            function count() {
                state.currentTime -= 1;
                if(state.currentTime === 0) {
                    commit("TOMAEVENT", false);
                    clearInterval(state.running)
                    commit('RUNNING', false);

                    dispatch('timerReset');
                    commit('STATUSSWITCH', !state.statusSwitch);
                }
            }
        },
        // 暫停計時
        pause({state, commit}) {
            clearInterval(state.running)
            commit('RUNNING', false);
        },
        // 重設計時器
        timerReset({ state, commit }) {
            if(state.statusSwitch) {
                commit('CURRENTTIME', state.workTime);
            }else {
                commit('CURRENTTIME', state.restTime);
            }    
        },
        // todo finish
        todoFinish({ rootState, dispatch, commit}) {
            commit("TOMAEVENT", true);
            // add one tomato to current todo
            let tomaNum = rootState.taskList.firstTask[0].tomato || 0;
            let id = rootState.taskList.firstTask[0].id;
            let status = {
                tomaNum,
                id,
            };
            dispatch('completeTodo', status);
            // remove current todo
            dispatch('countDown');  
        },
        // todo continue
        todoContinue({ rootState, dispatch, commit}) {
            commit("TOMAEVENT", true);
            // add one tomato to current todo
            if (rootState.taskList.firstTask[0].tomato) {
                rootState.taskList.firstTask[0].tomato += 1;
            }else {
                rootState.taskList.firstTask[0].tomato = 1;
            }
            dispatch('countDown');
        }
    },
    mutations: {
        WORKTIME(state, status) {
            state.workTime = status;
        },
        RESTTIME(state, status) {
            state.restTime = status;
        },
        CURRENTTIME(state, status) {
            state.currentTime = status;
        },
        RUNNING(state, status) {
            state.running = status;
        },
        STATUSSWITCH(state, status) {
            state.statusSwitch = status;
        },
        TOMAEVENT(state, status) {
            state.tomaEvent = status;
        }
    },
    getters: {
        workTime: state => state.workTime,
        restTime: state => state.restTime,
        currentTime: state => state.currentTime,
        running: state => state.running,
        statusSwitch: state => state.statusSwitch,
        tomaEvent: state => state.tomaEvent,
    }
}