<template>
    <div>
        <div class="timer display-3 roboMono text-center wordSpacing">
            {{ currentTime | timer }}
        </div>
        <div v-if="!statusSwitch" class="timerControll d-flex justify-content-center align-items-center mt-5">
            <div class="w-50">
                <button class="btn btn-block btn-tomaRed roboMono rounded-pill" @click="countDown" v-if="!running"><span class="wordSpacing text-center">start</span></button>
                <button class="btn btn-block btn-tomaRed roboMono rounded-pill" @click="pause" v-else><span class="wordSpacing text-center">pause</span></button>
            </div>
            <div class="refresh">
                <button class="btn" type="button" @click="timerReset">
                    <i class="fas fa-redo fa-2x text-tomaRed"></i>
                </button>
            </div>
        </div>
        <div v-else class="todoStatus container mt-5">
            <div class="row">
                <div class="col-6">
                    <button class="btn btn-block btn-tomaRed roboMono" type="button" @click="todoContinue" :disabled="tomaEvent">continue</button>
                </div>
                <div class="col-6">
                    <button class="btn btn-block btn-tomaRed roboMono" type="button" @click="todoFinish" :disabled="tomaEvent">finish</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
    methods: {
        ...mapActions(['countDown', 'pause', 'todoFinish', 'todoContinue']),
        // 重設計時器
        timerReset() {
            this.$store.dispatch('timerReset');   
        },
    },
    computed: {
        ...mapGetters(['workTime', 'restTime', 'currentTime', 'running', 'statusSwitch', 'tomaEvent'])
    },
    filters: {
        timer(second) {
            let min = Math.floor(second / 60);
            let sec = second % 60;
            //修正格式
            if(min < 10) {
                min = "0" + min;
            }
            if(sec < 10) {
                sec = "0" + sec;
            }
            let str = min + ":" + sec;
            return str
        }
    }
}
</script>