<template>
    <div>
        <div class="container text-center">
            <h1>Firebase Auth Test</h1>
            <div class="row justify-content-center mt-5">
                <div class="col-4">
                    <button type="button" class="btn btn-primary d-none d-md-block" @click="ISOlogin">使用google註冊(Popup)</button>
                    <button type="button" class="btn btn-primary d-md-none" @click="ISOlogin">使用google註冊(redirect)</button>
                </div>
                <div class="col-4">
                    <button type="button" class="btn btn-primary" @click="">disabled</button>
                </div>
            </div>
            <div class="row justify-content-center mt-5">
                <div class="col-4">
                    <button type="button" class="btn btn-primary" @click="getCurrentUser">查詢當前使用者</button>
                </div>
                <div class="col-4">
                    <button type="button" class="btn btn-danger" @click="signOut">登出</button>
                </div>
            </div>
            <div class="row justify-content-center mt-5">
                <div class="col-4">
                    <button type="button" class="btn btn-primary" @click="toIndex">前往首頁</button>
                </div>
            </div>
            <div id="firebaseui-auth-container"></div>
        </div>
    </div>
</template>

<script>
    
export default {
    data() {
        return {
            window: '',
            data: {},
        }
    },
    methods: {
        authPop(device) {
            this.$store.dispatch('authPop', device);
        },
        // ISOlogin() {
        //     this.$store.dispatch('ISOlogin');
        // },
        ISOlogin() {        
            const vm = this;
            // FirebaseUI config.
            var uiConfig = {
              callbacks: {
                signInSuccessWithAuthResult: function(authResult, redirectUrl) {
                    alert("success");
                    this.data = authResult;
                    // localStorage.setItem('user', JSON.stringify({userName, userID}));
                    return false;
                },
                signInFailure: function(error) {
                    return handleUIError(error);
                },
                
              },
              signInFlow: 'redirect',
              signInOptions: [
                firebase.auth.GoogleAuthProvider.PROVIDER_ID,
              ],
            };
            var ui = new firebaseui.auth.AuthUI(firebase.auth());
            ui.start('#firebaseui-auth-container', uiConfig);
        },
        getCurrentUser() {  
            this.$store.dispatch('getCurrentUser')
        },
        signOut() {
            firebase.auth().signOut().then(function() {
                // Sign-out successful.
                sessionStorage.clear();
                console.log("User signOut")
            }).catch(function(error) {
                // An error happened.
                return error;
            });
        },
        toIndex() {
            this.$router.push("/index")
        }
    },
    mounted() {
        // const vm = this;
        //  window.addEventListener('load', function() {
        //     vm.$store.dispatch("testAuth");
        // });
    },
}
</script>