<template>
    <div>
        <div class="outer">
            <div class="sloganWrap">
                <div class="tomaSlogan slogan1 font-weight-bold">
                    <p>Never Give Up</p>
                </div>
                <div class="tomaSlogan slogan2 font-weight-bold">
                    <p>Just do it !</p>
                </div>
            </div>
            <div class="tomatoLeaf">
                <div class="ovalWrap oval1">
                <div class="halfOval"></div>
                </div>
                <div class="ovalWrap oval2">
                <div class="halfOval"></div>
                </div>
                <div class="ovalWrap oval3">
                <div class="halfOval"></div>
