</template>

<script>
export default {
    data() {
        return {
            active: false
        }
    },
    computed: {
        spinActive() {
            if(this.$store.state.tomaTimer.running) {
                this.active = true;
            }else {
                this.active = false;
            }
        }
    },
}
</script>