   
// set provider
var provider = new firebase.auth.GoogleAuthProvider();

export default {
    state: {

    },
    actions: {
        ISOlogin({dispatch}) {
            // var signInFlow = 'redirect';
            // // For iOS full screen apps we use the redirect auth mode.
            // if (('standalone' in window.navigator)
            //     && window.navigator.standalone){
            //   signInFlow = 'redirect';
            // }
          
            // FirebaseUI config.
            var uiConfig = {
              callbacks: {
                signInSuccessWithAuthResult: function(authResult, redirectUrl) {
                    console.log(authResult);
                    alert(authResult);
                    return true;
                },
                signInFailure: function(error) {
                    alert(error);
                }
                
              },
              'signInFlow': 'redirect',
              'signInOptions': [
                firebase.auth.GoogleAuthProvider.PROVIDER_ID,
              ],
            };
            var ui = new firebaseui.auth.AuthUI(firebase.auth());
            ui.disableAutoSignIn();
            ui.start('#firebaseui-auth-container', uiConfig);
        },
        testAuth() {
            alert("testAuth");
        },
        authPop({ commit }, status) {
            var AuthWay = "signInWithPopup";
            if(status == 'phone') {
                AuthWay = "signInWithRedirect";
                alert("signInWithRedirect");
            }

            firebase.auth()[AuthWay](provider).then(function(result) {
                // 可以獲得 Google 提供 token，token可透過 Google API 獲得其他數據。  
                return result.user;
            }).then((user) => {

                // 登記資料
                const {displayName : userName,  uid: userID  } = user;
                commit('USER', {userName, userID});
                localStorage.setItem('user', JSON.stringify({userName, userID}));

                // 驗證帳戶姓名
                var IDRef = firebase.database().ref("/identity/");

                IDRef.child(userName).once("value").then(function(snapshot) {    
                    // 取得使用者工作清單
                    var userRef = firebase.database().ref(`${userID}`);
                    if( !snapshot.val() ) {
                        // 若為新用戶
                        // 登記資料
                        IDRef.child(userName).set({
                            "name"   : userName,
                            "userID" : userID
                        }).catch((error) => error);
                        // 新建工作清單
                        var date = new Date();
                        var timeStamp = date.getTime();
                        userRef.push({
                            id: timeStamp,
                            work: "新建一個 Todo",
                            tomato: 0,
                            isComplete: false
                        });
                        // build complete list
                        firebase.database().ref(`${userID}/complete List`).push({
                            id: timeStamp,
                            work: "歡迎使用 Tomado",
                            tomato: 0,
                            isComplete: false
                        })
                        console.log("hello new User")
                    }
                })   
            }).catch(function(err) {
                    return err;
                })
        },
        getCurrentUser({ commit }) {  
            const user = firebase.auth().currentUser;
            // if(user) {
            //     const {displayName : userName,  uid: userID  } = user;
            //     commit('USER', {userName, userID})
            //     sessionStorage.setItem('user', JSON.stringify({userName, userID}));
            // }else {
            //     console.log('未登入')
            //     return "";
            // }    
            alert(user.displayName);
        },
    },
    mutations: {

    },
    getters: {

    }
}